.todolist ul {
    margin: 0;
    padding: 0;
  }
  
  /* Style the list items */
  .todolist ul li {
    cursor: pointer;
    position: relative;
    padding: 12px 8px 12px 40px;
    list-style-type: none;
    background: #eee;
    font-size: 18px;
    transition: 0.2s;
    
    /* make the list items unselectable */
    -webkit-user-select: none;
    user-select: none;
  }
  
  /* Set all odd list items to a different color (zebra-stripes) */
  .todolist ul li:nth-child(odd) {
    background: #f9f9f9;
  }
  
  /* Darker background-color on hover */
  .todolist ul li:hover {
    background: #ddd;
  }
  
  /* When clicked on, add a background color and strike out text */
  .todolist ul li.checked {
    background: #888;
    color: #fff;
    text-decoration: line-through;
  }
  
  /* Add a "checked" mark when clicked on */
  .todolist ul li.checked::before {
    content: '';
    position: absolute;
    border-color: #fff;
    border-style: solid;
    border-width: 0 2px 2px 0;
    top: 10px;
    left: 16px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    height: 15px;
    width: 7px;
  }
  
  /* Style the close button */
  .todolist .close {
    position: absolute;
    right: 0;
    top: 0;
    padding: 12px 16px 12px 16px;
  }
  
  .todolist .close:hover {
    background-color: #145cd8;
    color: white;
  }
  
  /* Style the header */
  .todolist .header {
    background-color: #33599a;
    padding: 30px 40px;
    color: white;
    text-align: center;
    width: 600px;
  }
  
  /* Clear floats after the header */
  .todolist .header:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Style the input */
  .todolist input {
    margin: 0;
    border: none;
    border-radius: 0;
    width: 75%;
    padding: 10px;
    float: left;
    font-size: 16px;
  }
  
  /* Style the "Add" button */
  .todolist .addBtn {
    padding: 10px;
    width: 25%;
    background: #d9d9d9;
    color: #555;
    float: left;
    text-align: center;
    font-size: 16px;
    cursor: pointer;
    transition: 0.3s;
    border-radius: 0;
  }
  
  .todolist .addBtn:hover {
    background-color: #bbb;
  }

.accordion-cont{
    margin-top: 40px;
    max-width: 800px;
    margin:0 auto;
    background-color: rgb(155, 204, 222);
    border-radius: 10px;
    padding: 10px 30px;
}
.accordion-collapse{
    background-color: #ececec;
}
button:focus{
    outline: 0;
    border: none;
}
.accordion-button:focus{
    border: none !important;
    box-shadow: none;
    background-color: #e7f1ff;
}

.createnote{
    width: 400px;
    padding: 20px;
    margin:30px auto;
    background-color: #f8f9fa;
    border-radius: 6px;
    box-shadow: 0 0 5px #ccc;
    position: relative;

}
.createnote input, .createnote textarea{
    border: none;
    padding: 10px;
    width: 100%;
    font-size: 14px;
}
.createnote input:focus, .createnote textarea:focus,
.createnote input:active, .createnote textarea:active{
    outline: none;
    border: none;
    box-shadow: none;
    resize: none;
}
.createnote input{
    font-size: 16px;
    font-weight: bold;
}
